import React, { useRef } from "react";
import { Button, message } from "antd";

let stream;

const MediaStreamAPI = () => {
  const videoRef = useRef();

  async function openDevice() {
    try {
      stream = await navigator.mediaDevices.getUserMedia({
        audio: true,
        video: true,
      });
      videoRef.current.srcObject = stream;
    } catch (error) {
      if (error.name === "ConstraintNotSatisfiedError") {
        const v = constraints.video;
        message.error(`:${v.width.exact} :${v.height.exact} `);
      } else if (error.name === "PermissionDeniedError") {
        message.error(",");
      }
      message.error("getUserMedia:", error);
    }
  }

  function btnGetTracks() {
    console.log("btnGetTracks()");
    console.log(stream.getTracks());
  }

  function btnGetAudioTracks() {
    console.log("btnGetAudioTracks()");
    console.log(stream.getAudioTracks());
  }

  function btnGetVideoTracks() {
    console.log("btnGetVideoTracks()");
    console.log(stream.getVideoTracks());
  }

  function btnRemoveAudioTracks() {
    console.log("btnGetVideoTracks()");
    stream.removeTrack(stream.getAudioTracks()[0]);
  }

  function btnGetTrackById() {
    console.log("btnGetTrackById()");
    console.log(stream.getTrackById(stream.getAudioTracks()[0].id));
  }

  return (
    <div className="container">
      <h1>
        <span></span>
      </h1>
      <video className="video" ref={videoRef} autoPlay playsInline />
      <Button onClick={openDevice}></Button>
      <Button onClick={btnGetTracks}></Button>
      <Button onClick={btnGetAudioTracks}></Button>
      <Button onClick={btnGetVideoTracks}></Button>
      <Button onClick={btnRemoveAudioTracks}></Button>
      <Button onClick={btnGetTrackById}>Id</Button>
    </div>
  );
};

export default MediaStreamAPI;
