import React, { useEffect, useRef } from "react";
import { Button } from "antd";

let localStream;
let peerConnA;
let peerConnB;

const PeerConnection = () => {
  const localVideoRef = useRef();
  const remoteVideoRef = useRef();

  useEffect(() => {
    const localVideo = localVideoRef && localVideoRef.current;
    const remoteVideo = remoteVideoRef && remoteVideoRef.current;
    if (!localVideo) return;
    if (!remoteVideo) return;

    const loadedMetadataLister = () => {
      console.log(
        ":",
        localVideo.videoWidth,
        localVideo.videoHeight
      );
    };
    const remoteMetadataLister = () => {
      console.log(
        `:${remoteVideo.videoWidth} ${remoteVideo.videoHeight}`
      );
    };
    const resize = () => {
      console.log(
        `:${remoteVideo.videoWidth} ${remoteVideo.videoHeight}`
      );
    };

    localVideo.addEventListener("loadedmetadata", loadedMetadataLister);
    remoteVideo.addEventListener("loadedmetadata", remoteMetadataLister);
    remoteVideo.addEventListener("resize", resize);
    return () => {
      localVideo.removeEventListener("loadedmetadata", loadedMetadataLister);
      remoteVideo.removeEventListener("loadedmetadata", remoteMetadataLister);
      remoteVideo.removeEventListener("resize", resize);
    };
  });

  async function start() {
    try {
      localStream = await navigator.mediaDevices.getUserMedia({
        audio: true,
        video: true,
      });
      localVideoRef.current.srcObject = localStream;
    } catch (e) {
      console.log("getUserMedia:" + e);
    }
  }

  async function call() {
    //
    const videoTracks = localStream.getVideoTracks();
    //
    const audioTracks = localStream.getAudioTracks();
    //
    if (videoTracks.length > 0) {
      //
      console.log(`: ${videoTracks[0].label}`);
    }
    //
    if (audioTracks.length > 0) {
      //
      console.log(`: ${audioTracks[0].label}`);
    }

    let configuration = {
      iceServers: [{ url: "stun:stun.l.google.com:19302" }],
    };

    peerConnA = new RTCPeerConnection(configuration);
    peerConnA.addEventListener("icecandidate", onIceCandidateA);

    peerConnB = new RTCPeerConnection(configuration);
    peerConnB.addEventListener("icecandidate", onIceCandidateB);

    peerConnA.addEventListener("iceconnectionstatechange", onIceStateChangeA);
    peerConnB.addEventListener("iceconnectionstatechange", onIceStateChangeB);

    peerConnB.addEventListener("track", gotRemoteStream);

    localStream.getTracks().forEach((track) => {
      peerConnA.addTrack(track, localStream);
    });

    try {
      const offer = await peerConnA.createOffer();
      await onCreateOfferSuccess(offer);
    } catch (e) {
      onCreateSessionDescriptionError(e);
    }
  }

  async function onCreateOfferSuccess(desc) {
    console.log(`peerConnAOfferSDP\n${desc.sdp}`);
    console.log("peerConnAstart");
    try {
      await peerConnA.setLocalDescription(desc);
      onSetLocalSuccess(peerConnA);
    } catch (e) {
      onSetSessionDescriptionError(e);
    }

    try {
      await peerConnB.setRemoteDescription(desc);
      onSetRemoteSuccess(peerConnB);
    } catch (e) {
      onSetSessionDescriptionError(e);
    }

    try {
      const answer = await peerConnB.createAnswer();
      onCreateAnswerSuccess(answer);
    } catch (e) {
      onCreateSessionDescriptionError(e);
    }
  }

  async function onCreateAnswerSuccess(desc) {
    console.log(`peerConnBAnswer:\n${desc.sdp}`);
    console.log("peerConnB:setLocalDescription");
    try {
      await peerConnB.setLocalDescription(desc);
      onSetLocalSuccess(peerConnB);
    } catch (e) {
      onSetSessionDescriptionError(e);
    }

    try {
      await peerConnA.setRemoteDescription(desc);
      onSetRemoteSuccess(peerConnA);
    } catch (e) {
      onSetSessionDescriptionError(e);
    }
  }

  function onIceStateChangeA(event) {
    console.log(`peerConnAICE: ${peerConnA.iceConnectionState}`);
    console.log("ICE: ", event);
  }

  function onIceStateChangeB(event) {
    console.log(`peerConnBICE: ${peerConnA.iceConnectionState}`);
    console.log("ICE: ", event);
  }

  function onSetLocalSuccess(pc) {
    console.log(`${getName(pc)}:setLocalDescription`);
  }

  function onSetRemoteSuccess(pc) {
    console.log(`${getName(pc)}:setRemoteDescription`);
  }

  function getName(pc) {
    return pc === peerConnA ? "peerConnA" : "peerConnB";
  }

  function onCreateSessionDescriptionError(error) {
    console.log(`SD: ${error.toString()}`);
  }

  function onSetSessionDescriptionError(error) {
    console.log(`SD: ${error.toString()}`);
  }

  async function onIceCandidateA(event) {
    try {
      if (event.candidate) {
        await peerConnB.addIceCandidate(event.candidate);
        onAddIceCandidateSuccess(peerConnB);
      }
    } catch (e) {
      onAddIceCandidateError(peerConnA, e);
    }
    console.log(
      `IceCandidate:\n${
        event.candidate ? event.candidate.candidate : "(null)"
      }`
    );
  }

  async function onIceCandidateB(event) {
    try {
      if (event.candidate) {
        await peerConnA.addIceCandidate(event.candidate);
        onAddIceCandidateSuccess(peerConnA);
      }
    } catch (e) {
      onAddIceCandidateError(peerConnB, e);
    }
    console.log(
      `IceCandidate:\n${
        event.candidate ? event.candidate.candidate : "(null)"
      }`
    );
  }

  function onAddIceCandidateSuccess(pc) {
    console.log(`${getName(pc)}IceCandidate`);
  }

  function onAddIceCandidateError(pc, error) {
    console.log(`${getName(pc)}IceCandidate: ${error.toString()}`);
  }

  function gotRemoteStream(e) {
    if (remoteVideoRef.current.srcObject !== e.streams[0]) {
      remoteVideoRef.current.srcObject = e.streams[0];
    }
  }

  function hangup() {
    console.log("");
    peerConnA.close();
    peerConnB.close();
    peerConnA = null;
    peerConnB = null;
  }

  return (
    <div className="container">
      <h1>
        <span></span>
      </h1>
      <video ref={localVideoRef} playsInline autoPlay muted />
      <video ref={remoteVideoRef} playsInline autoPlay />
      <Button onClick={start}></Button>
      <Button onClick={call}></Button>
      <Button onClick={hangup}></Button>
    </div>
  );
};

export default PeerConnection;
