import React, { useRef, useEffect } from "react";
import { message, Select } from "antd";

import "../../styles/css/video-filter.scss";

const { Option } = Select;

const VideoFilter = () => {
  const videoRef = useRef();

  useEffect(() => {
    openCamera();
  }, []);

  async function openCamera() {
    try {
      const constraints = { audio: false, video: true };
      const stream = await navigator.mediaDevices.getUserMedia(constraints);
      const videoTracks = stream.getVideoTracks();
      console.log(":" + videoTracks[0]);
      const audioTracks = stream.getAudioTracks();
      console.log(":" + audioTracks[0]);
      videoRef.current.srcObject = stream;
    } catch (error) {
      if (error.name === "ConstraintNotSatisfiedError") {
        const v = constraints.video;
        message.error(`:${v.width.exact} :${v.height.exact} `);
      } else if (error.name === "PermissionDeniedError") {
        message.error(",");
      }
      message.error("getUserMedia:", error);
    }
  }

  function handChange(value) {
    videoRef.current.className = value;
  }

  return (
    <div className="container">
      <h1>
        <span></span>
      </h1>
      <video ref={videoRef} autoPlay playsInline></video>
      <Select
        defaultValue="none"
        style={{ width: "100px" }}
        onChange={handChange}
      >
        <Option value="none"></Option>
        <Option value="blur"></Option>
        <Option value="grayscale"></Option>
        <Option value="invert"></Option>
        <Option value="sepia"></Option>
      </Select>
    </div>
  );
};

export default VideoFilter;
