import React, { useRef } from "react";
import { Button } from "antd";

const Camera = () => {
  const videoRef = useRef();

  async function openCamera() {
    try {
      const constraints = { audio: false, video: true };
      const stream = await navigator.mediaDevices.getUserMedia(constraints);
      const videoTracks = stream.getVideoTracks();
      console.log(":" + videoTracks[0]);
      const audioTracks = stream.getAudioTracks();
      console.log(":" + audioTracks[0]);
      videoRef.current.srcObject = stream;
    } catch (error) {
      if (error.name === "ConstraintNotSatisfiedError") {
        const v = constraints.video;
        message.error(`:${v.width.exact} :${v.height.exact} `);
      } else if (error.name === "PermissionDeniedError") {
        message.error(",");
      }
      message.error("getUserMedia:", error);
    }
  }

  return (
    <div className="container">
      <h1>
        <span></span>
      </h1>
      <video className="video" ref={videoRef} autoPlay playsInline />
      <Button onClick={openCamera}></Button>
    </div>
  );
};

export default Camera;
